.Toggle_slider__1Lgir {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: .4s;
}

.Toggle_slider__1Lgir:before {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  transition: .4s;
}

input:checked + .Toggle_slider__1Lgir {
  background-color: #a0372d;
}

input:focus + .Toggle_slider__1Lgir {
  box-shadow: 0 0 1px #a0372d;
}

input:checked + .Toggle_slider__1Lgir:before {
  -webkit-transform: translateX(20px);
  transform: translateX(20px);
}

